<template>
  <v-app :dark="dark">
    <v-content>
      <v-container>
        <v-layout wrap>
          <v-flex xs12>
            <v-layout>
              <v-flex xs3 sm3 offset-xs9 offset-sm11>
                <v-icon large @click="changeTheme()">brightness_6</v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs12 sm6 offset-sm3 mt-2>
            <v-card>
              <v-card-title>
                <v-layout wrap>
                  <v-flex xs12>
                    <v-select
                        :items="groups"
                        v-model="group"
                        label="Выбери группу"
                        outline
                        v-on:change="selectedGroupsCount = 2, selectedStudents = []"
                    ></v-select>
                  </v-flex>
                  <v-flex xs12>
                    <v-select
                          v-model="selectedStudents"
                          :items="studentssss"
                          label="Среди всех кроме ..."
                          multiple
                    >
                      <v-divider slot="append-item" class="mb-2"></v-divider>
                      <v-list-tile slot="append-item">
                        <v-list-tile-avatar color="grey lighten-3">
                          <v-icon>face</v-icon>
                        </v-list-tile-avatar>
                        <v-list-tile-content>
                          <v-list-tile-title>Количество учеников</v-list-tile-title>
                          <v-list-tile-sub-title>{{ selectedStudents.length }}</v-list-tile-sub-title>
                        </v-list-tile-content>
                      </v-list-tile>
                    </v-select>
                  </v-flex>
                  <v-flex xs12>
                    <v-radio-group v-model="radio">
                      <v-radio value="generateList">
                        <div slot="label">Сгенерировать рандомный список</div>
                      </v-radio>
                      <v-radio value="divideToGroups">
                        <div slot="label" class="in-one-line">
                          <p class="pt-4">Поделить на</p>
                          <v-select
                                  v-model="selectedGroupsCount"
                                  :items="groupsCount"
                                  class="ml-2 mr-2"></v-select>
                          <p class="pt-4">группы</p>
                        </div>
                      </v-radio>
                    </v-radio-group>
                  </v-flex>
                </v-layout>
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" persistent scrollable max-width="700px">
                  <v-btn
                          slot="activator"
                          class="primary mr-2 mb-2"
                          @click="randomize()">Randomize!</v-btn>
                  <v-card>
                    <v-card-title class="headline">
                      <div v-if="radio === 'generateList'">Рандомный список</div>
                      <div v-else>Рандомные {{ selectedGroupsCount }} группы</div>
                    </v-card-title>
                    <v-card-text>
                      <div v-if="radio === 'generateList'">
                        <p v-for="(student, index) in studentsss" :key="index">
                          {{ index + 1 }}) {{ student }}
                        </p>
                      </div>
                      <div v-else>
                        <div v-for="(item, index) in generatedGroups" :key="index">
                          <strong>{{ index + 1 }} Группа:</strong>
                          <span v-for="miniItem in item" :key="miniItem">
                            {{ miniItem }}
                          </span>
                        </div>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn class="primary" @click.native="dialog = false">OK</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
    <v-footer class="pa-3">
      <div>
        Made with
        <v-icon class="red--text">favorite</v-icon>
        by <a class="cyan--text" href="https://vk.com/ssyernar" target="_blank">Yernar Sadybekov</a>
      </div>
      <v-spacer></v-spacer>
      <div>&copy; {{ new Date().getFullYear() }}</div>
    </v-footer>

  </v-app>
</template>

<script>
function shuffle(a) {
    let j, x, i;
    for (i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
    }
    return a
}

export default {
  name: 'App',
  data: () => ({
      dark: false,
      radio: "generateList",
      dialog: false,
      group: "Класс",
      selectedStudents: [],
      selectedGroupsCount: 2,
      groups: [
          "Класс", "1-ая группа", "2-ая группа"
      ],
      groupsCountt: [
          2, 3 ,4, 6
      ],
      students: ["Аружан", "Толкынай", "Жания", "Дамиля", "Анель", "Данай",
          "Арай", "Ернар", "Ажар", "Айнур", "Сайфуддин", "Алан", "Олжас",
          "Елигай", "Маулен", "Амина", "Нуржигит", "Алихан", "Зере", "Мухсин",
          "Тогжан", "Сырым", "Акежан", "Дарын"].sort(),
      secondGroupStudents: ["Аружан", "Дамиля", "Анель", "Арай", "Ернар",
          "Алан", "Олжас", "Маулен", "Нуржигит", "Алихан", "Акежан", "Дарын"].sort(),
      studentsss: [],
      generatedGroups: null
  }),
  methods: {
      changeTheme() {
          this.dark = !this.dark
      },
      randomize() {
          let rnd = shuffle(this.studentss)
          let sz = rnd.length
          if(this.radio === "generateList")
              this.studentsss = rnd
          else {
              let tmp = Math.floor(sz / this.selectedGroupsCount)
              let r = sz, arr = [], it = 0, cnt = this.selectedGroupsCount
              for(let i = 0; i < this.selectedGroupsCount; i++) {
                  arr.push([])
              }
              for(let i = 0; i < this.selectedGroupsCount; i++) {
                  if(r % cnt-- !== 0) {
                      r -= (tmp + 1)
                      for (let l = 1; l <= tmp + 1; l++) {
                          arr[i].push(rnd[it++])
                      }
                  }
                  else {
                      r -= tmp
                      for(let l = 1; l <= tmp; l++) {
                          arr[i].push(rnd[it++])
                      }
                  }
              }
              this.generatedGroups = [...arr]
          }
      }
  },
  computed: {
      groupsCount() {
          if(this.group === "Класс")
              return [...this.groupsCountt, 12]
          else
              return this.groupsCountt
      },
      firstGroupStudents() {
          let arr = [...this.students]
          for(let i = 0; i < this.secondGroupStudents.length; i++) {
              let index = arr.indexOf(this.secondGroupStudents[i])
              arr.splice(index, 1)
          }
          return arr
      },
      studentss() {
          if(this.group === "Класс") {
              let arr = [...this.students]
              for(let i = 0; i < this.selectedStudents.length; i++) {
                  let index = arr.indexOf(this.selectedStudents[i])
                  arr.splice(index, 1)
              }
              return arr
          }
          if(this.group === "1-ая группа") {
              let arr = [...this.firstGroupStudents]
              for(let i = 0; i < this.selectedStudents.length; i++) {
                  let index = arr.indexOf(this.selectedStudents[i])
                  arr.splice(index, 1)
              }
              return arr
          }
          let arr = [...this.secondGroupStudents]
          for(let i = 0; i < this.selectedStudents.length; i++) {
              let index = arr.indexOf(this.selectedStudents[i])
              arr.splice(index, 1)
          }
          return arr

      },
      studentssss() {
          if(this.group === "Класс")
              return this.students
          if(this.group === "1-ая группа")
              return this.firstGroupStudents
          return this.secondGroupStudents
      }
  }
}
</script>

<style>
  a {
    text-decoration: none;
    font-weight: bold;
  }
  .in-one-line {
    display: flex;
    justify-content: space-between;
  }
  .v-select__selections {
    width: 25px;
  }
</style>
